<template>
  <v-snackbar top right v-model="alertVisible" :timeout="alert.timeout" :multi-line="alert.multiline === true">
    {{ alert.text }}

    <v-btn flat @click="closeAlert">
      <v-icon>close</v-icon>
    </v-btn>
  </v-snackbar>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Alert",
  data() {
    return {
      alertVisible: false
    };
  },
  computed: {
    ...mapGetters(["alert", "hasError"])
  },
  watch: {
    hasError(value) {
      if (value) {
        this.alertVisible = true;
      }
    },
    alertVisible(value) {
      if (!value) {
        this.$store.dispatch("closeAlert");
      }
    }
  },
  methods: {
    closeAlert() {
      this.alertVisible = false;
      this.$store.dispatch("closeAlert");
    }
  }
};
</script>



